.portfolio{
    
    

    h1{
        scrollbar-width: none;  //firefox
        &::-webkit-scrollbar{
            display: none;
        }
        font-weight: 700;
    }

    .card{
        // border: 1px solid red;
        transition: all 0.2s ease;
        cursor: pointer;
        overflow: hidden;
        &:hover{
             transform: scaleX(1.05);

        }


    }

    .card-title{
        scrollbar-width: none;  //firefox
        &::-webkit-scrollbar{
            display: none;
        }
    }
    .card-header{
        font-weight: 700;
        font-size: 26px;
    }

   
    
}