@import "../../global.scss";
.contact{
    background-color: white;
    display: flex;

    
    @include mobile {
        flex-direction: column;
        
    }

    .left{
        flex: 1;

        img{
            height: 100%;
            overflow: hidden;
            @include tablet {
                width:110%;
                
            }
            @include mobile {
                width:110%;
                
            }
        }
    }
    .right{
        flex:1;

        h2{
            font-size: 30px;
            font-weight: 700;
            overflow: hidden;
        }

        span{
            color: green;
        }

        
    }
}