@import "../../global.scss";
.intro{
    background-color: white;
    display: flex;
   @include mobile{
    flex-direction: column;
    align-items: center;
   }
   @include tablet{
    flex-direction: column;
    align-items: center;
   }
    .left{
      flex:0.5;
      overflow: hidden;

      .imgContainer{
        width: 700px;
        height: 700px;
        background-color: rgb(102, 245, 51);
        clip-path: circle(50% at 50% 50%);
        display: flex;
        justify-content: center;
        align-items: center;
        float: right;
        
        @include mobile{
          align-items: flex-start;
          
          
        }
        @include tablet{
          align-items: flex-start;
          
          
        }

        img{
            width: 90%;
            height: 99%;

            @include mobile{
              height: 50%;
              width: 50%;
            }
            @include tablet{
              height: 50%;
              width: 50%;
            }
        }
      }
      
    }
    .right{
      flex: 0.5;
      position: relative;
      .wrapper{
          width: 100%;
          height: 100%;
          padding-left: 50px;
          display: flex;
          flex-direction: column;
          justify-content: center;

          @include mobile{
            padding-left: 0;
            align-items: center;
          }
          @include tablet{
            padding-left: 0;
            align-items: center;
          }

          h1{
            font-size: 60px;
            margin:10px 0;
            overflow: hidden;
            @include mobile{
              font-size: 40px;
              overflow: visible;
              margin: 5px 0;
            }
            @include tablet{
              font-size: 40px;
              overflow: visible;
              margin: 5px 0;
            }
          }
          h2{
            font-size: 35px;
            overflow: hidden;
            @include mobile{
              font-size: 20px;
              overflow: visible;
            }
            @include tablet{
              font-size: 20px;
              overflow: visible;
            }
          }
        span{
          font-size: 30px;
          color: crimson;
          font-weight: bold;
        }
        .skills{
          font-size: 30px;
          color: rgb(58, 5, 233);
          font-weight: bold;
          margin-top: 10px 0;
          @include mobile{
            font-size: 20px;
            overflow: visible;
            padding: 5px 5px;
          }
          @include tablet{
            font-size: 20px;
            padding: 5px 5px;
          }
        }
        .down{
          display: flex;
          justify-content: center;
          align-items: center;
          .downIcon{
            bottom: 10px;
            position: absolute;
            animation: arrowBlink 2s infinite;
            @include mobile{
               display: none;
            }
          }
          @keyframes arrowBlink {
            100%{
              opacity: 0;
            }
          }
        }
      }
    }
}